@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  display: flex;
  justify-content: center;  
  background:#001E26;
  margin: 0;
  font-family: 'Roboto', Arial, sans-serif;
}

h1 {
    color:#fff;
    max-width: 540px;
    margin-bottom: 10px;
    font-size: 1.6em;
    margin-bottom: 0px;
}

P {
    color: #fff;
    font-size: 0.8em;
    font-weight: 600;
    margin-bottom: 30px;
}

.main {
    display: flex;
    flex-direction: column;
    margin-top:20px;
    padding:50px;
}

.main img {
    width:100%;
    max-width: 190px;
    max-height: 190px;
    margin-bottom: 25px;
}

.main img:hover {
    cursor: pointer;
}
.convidados {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.Social button {
    width:100%;
    max-width: 240px;
    padding: 10px 20px 10px 20px;
    background-color: #DDE341;
    border: solid 0px;
    color: #2F2F2F;
    font-family: 'Roboto';
    font-weight: 600;
}

.Social {
    margin-top: 20px;
}

.Social button:hover {
    cursor: pointer;
    background-attachment:#DDE341;
    border: solid 0px;
}

.Social button:focus {
    border: solid 0px;
}
.Participante {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 300px;
    padding-right: 10px;
}

.Participante img {
    max-width: 100px;
    max-height: 100%;
    width: auto;
    height: auto;
    margin-right: 20px;
}

.Participante img:hover {
    cursor: pointer;    
}

.Participante h3 {
    color: #fff;
    margin:0;
}

.Participante p {
    color: #ccc;
}

.bio {
    display: flex;
    flex-direction: column;
}
